/* enter */
.fade-appear {
  opacity: 0;
}
.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 600ms linear;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 600ms linear;
}
/* exit */
.fade-exit {
  opacity: 1;
  display: none;
}
.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 1ms linear;
}
.fade-exit-done {
  opacity: 0;
}
