@import "../../../util/_variables.scss";
.Project {
  display: flex;
  flex-direction: column;
  padding: 1em;
  width: 33.3%;
  z-index: 2;

  .card {
    .title {
      padding-top: 2em;
      color: $white;
    }
    .description {
      min-height: 4em;
      color: $white;
    }
    .buttons {
      display: flex;
      .dark {
        border-radius: 8px;
        margin: 1em;
        padding: 1em;
        flex: 1;
        background-color: #30d0d0a4;
        box-shadow: 1px 1px 3px 1px black;
        border-color: darken($color: #172863, $amount: 5%);
        &:hover {
          background-color: #0f1429;
        }
      }
      .outlineDark {
        border-radius: 8px;
        margin: 1em;
        padding: 1em;
        flex: 1;
        box-shadow: 1px 1px 3px 1px black;
        background-color: transparent;
        border-color: #172042;
        &:hover {
          background-color: #172042;
        }
      }
      a {
        flex: 1;
        margin: 1%;
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .Project {
    width: 100%;
  }
}

@media only screen and (min-width: 600px) and (max-width: 1024px) {
  .Project {
    width: 50%;
  }
}
