@import "../util/_variables.scss";

.Contact {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 50%;
    z-index: 1;
    overflow: hidden;
    /*  change the hieght and the color below */
    border-bottom: 300px solid #12295c;
    -moz-transform: rotate(0.000001deg);
    -webkit-transform: rotate(0.000001deg);
    -o-transform: rotate(0.000001deg);
    -ms-transform: rotate(0.000001deg);
    transform: rotate(0.000001deg);
    right: 50%;
    border-right: 1500px solid transparent;
    border-left: 1500px solid;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 50%;
    z-index: 1;
    /*  change the hieght and the color below */
    border-bottom: 300px solid #12295c;
    -moz-transform: rotate(0.000001deg);
    -webkit-transform: rotate(0.000001deg);
    -o-transform: rotate(0.000001deg);
    -ms-transform: rotate(0.000001deg);
    transform: rotate(0.000001deg);
    left: 50%;
    border-left: 1500px solid transparent;
    border-right: 1500px solid;
  }

  .container {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateY(-50%);
    transform: translateX(-50%);
    .name {
      h3 {
        svg {
          fill: #ffffff;
          stroke: $blue;
        }
      }
      p {
        font-size: 2rem;
        color: $blue;
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .Contact {
    height: 50vh;
    .name {
      h3 {
        svg {
          width: 25rem;
          fill: #ffffff;
          stroke: red;
        }
      }
    }
  }
}
