@import "../util/_variables.scss";
.Skills {
  @include page;
  background-color: white;
  display: flex;
  overflow: hidden;
  padding-top: 20vh;
  width: 75%;
  margin: auto;
  height: 80vh;
  vertical-align: middle;
  canvas {
    width: 100% !important;
  }
  .container {
    z-index: 2;
    .skill {
      padding: 1em;
    }
    .progress {
      div {
        background-color: #12295c !important;
      }
    }
  }
  .cube {
    display: block;
  }
  svg {
    position: absolute;
    top: 50%;
    z-index: 5;
    color: rgba(255, 255, 255, 0.767);
    font-size: 3.5vw;
    cursor: pointer;
  }
  .arrowRight {
    right: 5%;
  }
  .arrowLeft {
    left: 5%;
  }
}

@media only screen and (max-width: 1024px) {
  .container {
    width: 90%;
    margin: auto;
  }
  canvas {
    display: none !important;
    width: 40% !important;
    z-index: 1;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -100%);
  }
  .cube {
    display: none;
  }
}
/* @media only screen and (min-width: 600px) and (max-width: 1024px) {
}
 */
@media only screen and (max-width: 768px) {
  .Skills {
    padding-top: 0;
  }
}
