@import "../util/_variables.scss";

.desktop {
  display: none;
}
.Navigation {
  position: absolute;
  z-index: 1000;
  background-color: transparent;
  left: 0;
  top: 0;
  width: 100%;
  height: 3em;
  .navToggler {
    width: min-content;
    cursor: pointer;
    padding: 1em;
    transition: 0.7s;
    transform: translateY(-5%);
  }

  .line {
    width: 25px;
    height: 3px;
    background-color: $blue;
    margin: 6px 0;
    transition: 1s;
  }
  .hamburger {
    @extend .navToggler;
    div:nth-child(1) {
      @extend .line;
      width: 30px;
    }
    div:nth-child(2) {
      @extend .line;
      width: 20px;
    }
  }
  .cross {
    @extend .navToggler;
    margin: 0.5em;
    padding-left: 0.5em;
    div:nth-child(1) {
      @extend .line;
      transform: rotate(-135deg);
      margin: 0px;
    }
    div:nth-child(2) {
      @extend .line;
      transform: rotate(135deg);
      margin: -3px 0;
    }
  }

  .navLinks {
    position: absolute;
    background-color: white;
    top: 100%;
    height: 100vh;
    width: 100%;
    transition: 0.5s;
    .container {
      position: absolute;
      top: 40%;
      left: 50%;
      transform: translate(-50%, -40%);
      a {
        color: $blue;
        li {
          padding: 1em;
          font-size: 2em;
          transition: 0.5s;
          &:hover {
            transform: scale(1.2);
            box-shadow: 0.1px 0.1px 1px black;
          }
        }
      }
    }
  }
  .expand {
    left: 0;
    @extend .navLinks;
  }

  .collapse {
    left: -100%;
    @extend .navLinks;
  }
}
