.Call {
  padding-bottom: 10vh;
  width: 75%;
  margin: auto;

  .textContainer {
    padding: 3rem;
    margin: 5rem;
  }

  .iconList {
    display: flex;
    vertical-align: middle;
    svg {
      margin-right: 1rem;
    }

    p {
      font-size: 1rem;
    }
  }
}

