.Projects {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: auto;
}

.backgroundText {
  background-image: linear-gradient(to top, #2e065f5e 0%, #30d0d06c 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  position: absolute;
  z-index: 1;
  font-size: 50vh;
}
.backgroundText1 {
  @extend .backgroundText;
  top: -1%;
  left: -49%;
}
.backgroundText2 {
  @extend .backgroundText;
  top: 40%;
  right: -60%;
}
