@mixin center($top, $left) {
  position: absolute;
  top: $top;
  left: $left;
  transform: translate(-$left, -$top);
}

/* mobile */
@mixin pageM($marginTop) {
  width: 100%;
  height: 80vh;
  margin-top: $marginTop * 1vh;
  position: relative;
}
